* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
}

.section {
  display: flex;
  flex-flow: column wrap;
  margin: 1rem;
  max-width: 1440px;
}
.section__figure {
  margin: 1rem auto;
}
.section__figure .section__img {
  content: url("/frontend-recruitment-task/images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_200.jpg");
}
.section__desc {
  display: flex;
  flex-flow: column;
}
.section__desc .section__title {
  color: #121212;
  font-size: 2.625rem;
  font-weight: 700;
  margin: 1.5rem 0;
}
.section__desc .section__text {
  color: #121212;
  font-weight: 300;
  line-height: 1.5rem;
}
.section__desc .section__btn {
  margin: 2rem auto;
  width: 8rem;
  padding: 1rem 2.5rem;
  border-radius: 2rem;
  border: none;
  background-color: #e4c1ae;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s linear;
}
.section__desc .section__btn:hover {
  background-color: #e59468;
}

@media only screen and (min-width: 768px) {
  .section {
    display: flex;
    flex-flow: column wrap;
    margin: 1rem 3rem;
    max-width: 1440px;
  }
  .section__figure {
    margin: 1rem auto;
  }
  .section__figure .section__img {
    content: url("/frontend-recruitment-task/images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_450.jpg");
  }
}
@media only screen and (min-width: 1280px) {
  .section {
    display: flex;
    flex-flow: column wrap;
    margin: 1rem 3rem;
    max-width: 1440px;
  }
  .section__figure {
    margin: 1rem auto;
  }
  .section__figure .section__img {
    content: url("/frontend-recruitment-task/images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_480.jpg");
  }
}